        <div class="background-black" @click="showCode = false" v-if="showCode"></div>

        <transition>
            <div v-if="showCode">
                <div class="show-code">
                    <div class="close" @click="showCode = false">x</div>
                    <h1>Joyeux NoÃ«l Guillaume!</h1>

                    <p>
                        Du coup je t'ai offert une carte cadeau Steam, j'espÃ¨re
                        que Ã§a te fais plaisir ðŸ˜…. <br />
                        J'en profite aussi pour te dire
                        <strong>MERCI</strong> pour tout. Merci Ã  toi et Ã  Fred
                        de nous accueillir FlÃ´ et moi pour les parties de
                        Pandemic, vous Ãªtes des hÃ´tes <strong>SUPER</strong>, je vous adore ! ðŸ˜˜
                    </p>
                    <p>Ah, et voilÃ  le code:</p>

                    <div class="steam-code" @click="copyCode">
                        <span style="font-family: auto; font-size: 24px">
                            LXKKK-XKRXT-0IWRI
                            <img src="./assets/copy.png" width="20" />
                        </span>
                    </div>
                </div>
            </div>
        </transition>

        <div
            class="window"
            @dblclick="change"
            :style="{
                transform: `translate(-50%, -50%) rotate(${rotation}deg)`,
                left: left,
            }"
        >
            <div v-show="step === 'SANTA'">
                <div class="santa">
                    <div class="head">
                        <div class="face">
                            <div class="redhat">
                                <div class="whitepart"></div>
                                <div class="redpart"></div>
                                <div class="hatball"></div>
                            </div>
                            <div class="eyes"></div>
                            <div class="beard">
                                <div class="nouse"></div>
                                <div class="mouth"></div>
                            </div>
                        </div>
                        <div class="ears"></div>
                    </div>
                    <div class="body"></div>
                </div>
            </div>

            <figure id="bridgeContainer" v-show="step === 'GRATTE'">
                <canvas id="bridge" width="826" height="826"></canvas>
            </figure>
        </div>
        <div class="message">
            <h1>{{ textTop }}</h1>
            <p>{{ textBottom }}</p>
            <transition name="fade">
                <button
                    class="button"
                    v-if="showButton"
                    @click="showCode = true"
                >
                    J'ai fini de gratter!
                </button>
            </transition>
        </div>
    </div>
