<script>
export default {
    name: "App",
    data() {
        return {
            step: "SANTA",
            left: "50%",
            rotation: 0,
            door: "close",
            textTop: "Joyeux NoÃ«l!",
            textBottom: "(Touche le pÃ¨re noÃ«l deux fois)",
            showButton: false,
            showCode: false,
            copied: false
        };
    },
    methods: {
        change() {
            if(this.step !== "SANTA")
                return;

            this.left = "-50%";
            this.rotation = 360;

            setTimeout(() => {
                this.step = "GRATTE";

                this.left = "50%";
                this.rotation = 0;

                setTimeout(() => {
                    this.textBottom =
                        "ðŸŽ Gratte pour dÃ©couvrir ton cadeau ! ðŸŽ";
                }, 1000);
            }, 1000);
        },
        copyCode(){
            const el = document.createElement('textarea');
            el.value = "LXKKK-XKRXT-0IWRI";
            document.body.appendChild(el);
            el.select();
            document.execCommand('copy');
            document.body.removeChild(el);
            this.copied = true;
            setTimeout(() => {
                this.copied = false;
            }, 5000); 
        }
    },
    mounted() {
        var bridge = document.getElementById("bridge"),
            bridgeCanvas = bridge.getContext("2d"),
            brushRadius = (bridge.width / 100) * 5,
            img = new Image();

        if (brushRadius < 50) {
            brushRadius = 50;
        }

        img.onload = function () {
            bridgeCanvas.drawImage(img, 0, 0, bridge.width, bridge.height);
        };
        img.src = require("./assets/background.png");

        function detectLeftButton(event) {
            if ("buttons" in event) {
                return event.buttons === 1;
            } else if ("which" in event) {
                return event.which === 1;
            } else {
                return event.button === 1;
            }
        }

        function getBrushPos(xRef, yRef) {
            var bridgeRect = bridge.getBoundingClientRect();
            return {
                x: Math.floor(
                    ((xRef - bridgeRect.left) /
                        (bridgeRect.right - bridgeRect.left)) *
                        bridge.width
                ),
                y: Math.floor(
                    ((yRef - bridgeRect.top) /
                        (bridgeRect.bottom - bridgeRect.top)) *
                        bridge.height
                ),
            };
        }

        let timeout;

        const drawDot = (mouseX, mouseY) => {
            if (!timeout) {
                timeout = setTimeout(() => {
                    this.showButton = true;
                }, 5000);
            }

            bridgeCanvas.beginPath();
            bridgeCanvas.arc(mouseX, mouseY, brushRadius, 0, 2 * Math.PI, true);
            bridgeCanvas.fillStyle = "#000";
            bridgeCanvas.globalCompositeOperation = "destination-out";
            bridgeCanvas.fill();
        };

        bridge.addEventListener(
            "mousemove",
            function (e) {
                var brushPos = getBrushPos(e.clientX, e.clientY);
                var leftBut = detectLeftButton(e);
                if (leftBut == 1) {
                    drawDot(brushPos.x, brushPos.y);
                }
            },
            false
        );

        bridge.addEventListener(
            "touchmove",
            function (e) {
                e.preventDefault();
                var touch = e.targetTouches[0];
                if (touch) {
                    var brushPos = getBrushPos(touch.pageX, touch.pageY);
                    drawDot(brushPos.x, brushPos.y);
                }
            },
            false
        );
    },
};
</script>

